import React, { Component } from 'react';
import GameQuestions from './Game_Questions';
import GameQuestionOver from './Game_Question_Over';
import ReconnectingWebSocket from 'reconnecting-websocket'
import { random } from 'lodash';

function get_cookie(name) {
    var value;
    if (document.cookie && document.cookie !== '') {
        document.cookie.split(';').forEach(function (c) {
            var m = c.trim().match(/(\w+)=(.*)/);
            if(m !== undefined && m[1] == name) {
                value = decodeURIComponent(m[2]);
            }
        });
    }
    return value;
}

class Game extends Component {
    constructor() {
        super();
        this.state = {
            triviaid: get_cookie("triviaid"),
            timer: 20,
            isLive: false,
            questionOver: false,
            gameOver: false,
            currentQuestion: 0,
            questions: [],
            players: [],
            playerCounter: 0,
            triviaName: '',
            courseName: '',
            leaderBoard: [],
            id: 0
        }
        this.questionOverFn = this.questionOverFn.bind(this);
        this.nextQuestion = this.nextQuestion.bind(this);
        this.timeKeeper = this.timeKeeper.bind(this);
        this.startGame = this.startGame.bind(this);
    }
    componentDidMount() {
        console.log("Changing socket: ")
        let ws_scheme = window.location.protocol === "https:" ? "wss" : "ws";
        let connection_url = ws_scheme + '://oneup.wssu.edu/ws/trivia/' + this.state.triviaID;

        this.socket = new ReconnectingWebSocket(connection_url);
        this.socket.send(JSON.stringify({event: 'host-join', user: this.state.user, triviaid: this.state.triviaid }));
        
        this.socket.onopen = function(){
            console.log("Connected to trivia socket");
        }
        this.socket.onclose = function(){
            console.log("Disconnected from trivia socket");
        }

        this.socket.onmessage = function(m){
            var message = JSON.parse(m.data);
            if(message.event === 'room-joined'){
                console.log('room-joined', message)
                this.addPlayer(message.name, message.id, message.avatarurl)
            } else if(message.event === 'room-left'){
                console.log('room-left', message)
                // this.removePlayer(message.id)
            } else if(message.event === 'player-answer'){
                console.log('player-answer', message)
                this.submitAnswer(message.name, message.answer)
            } else if(message.event === 'trivia-questions'){
                console.log('trivia-questions', message)
                this.setState({questions: message.questions})
            } else if (message.event === 'init_data'){
                console.log("init_data", message)
                this.setState({
                    triviaName: message.trivia_name,
                    courseName: message.course_name,
                    id: message.id,
                    name: message.name,
                })
            }
        }.bind(this)
    }
    startGame() {
            this.nextQuestion()
            this.setState({
                isLive: true
            })
    }
    questionOverFn() {
        let { players } = this.state
        this.socket.send(JSON.stringify({event: 'question-over', user: this.state.user}));
        let updatedPlayers = [...players];
        updatedPlayers.forEach((player)=>{
            player.qAnswered = false;
            player.answeredCorrect = false;
        })
        this.getLeaderBoard()
        this.setState({
            questionOver: true,
            currentQuestion: this.state.currentQuestion + 1,
            timer: 20,
            players: updatedPlayers
        })
    }
    timeKeeper() {
        let internalTimer = 20;
        let players = [...this.state.players]

        this.setState({ questionOver: false })

        timeCheck = timeCheck.bind(this)

        function timeCheck(){
            let checkAnswers = ()=>{
                let pAnswered = 0;
                players.forEach((player)=>{
                    if(player.qAnswered) {
                        pAnswered++
                    }
                })
                players.forEach(player => {
                    if(player.answeredCorrect){
                        player.score += (internalTimer*10 +1000)
                        this.socket.send(JSON.stringify({event: 'sent-info', player_id: player.id, score: player.score, answeredCorrect: player.answeredCorrect}));
                    }
                    
                });
                if (pAnswered === players.length) {
                    internalTimer = 0
                }
                if (internalTimer) {
                    internalTimer -= 1;
                }
            }
            let endQuestion = ()=>{
                clearInterval(timeKept);
                this.questionOverFn();
            }
            return internalTimer > 0 
            ? checkAnswers()
            : endQuestion()
        }
        let timeKept = setInterval(()=>{timeCheck()}, 1000);
        return timeKept
    }

    nextQuestion() {
        let { questions, currentQuestion } = this.state;
        this.timeKeeper();
        // this.hotTimer();
        console.log(questions);

        currentQuestion === questions.length 
            ? this.setState({ gameOver: true })
            : 
            this.socket.send(JSON.stringify({event: 'next-question'}));
            this.setState({questionOver:false})      
    }

    addPlayer(name, id, avatarurl) {
        // check if player is in newPlayers 
        let newPlayers = [...this.state.players]
        let playerExists = false;
        newPlayers.forEach((player)=>{
            if(player.name === name){
                playerExists = true;
            }
        })
        if(!playerExists){
            let player = {
                id: id, // this is now their socket id so they can pull their score to the player component using this
                name: name,
                score: 0,
                qAnswered: false,
                answeredCorrect: false,
                avatarurl: avatarurl
            }
            let newPlayerArr = [...this.state.players]
            newPlayerArr.push(player)
            this.setState({
                players: newPlayerArr,
                playerCounter: this.state.playerCounter + 1
            })
        }
    }

    submitAnswer(name, answer) {
        let player = this.state.players.filter(player => player.name === name);
        let updatedPlayers = this.state.players.filter(player => player.name !== name);
        console.log("my answer is: ", answer)
        console.log("the correct answer is: ", this.state.questions[this.state.currentQuestion].correctanswer)
        player[0].qAnswered = true;
        answer === this.state.questions[this.state.currentQuestion].correctanswer
            ?player[0].answeredCorrect = true
            :player[0].answeredCorrect = false

        updatedPlayers.push(player[0])
        this.setState({
            players: updatedPlayers
        })
        
    }

    getLeaderBoard() {
        let unsorted = [...this.state.players];
        let leaderboard = unsorted.sort((a, b) => b.score - a.score)
        console.log(leaderboard)
        this.setState({
            leaderBoard: leaderboard
        })
    }

    render() {
        let { triviaName, courseName, questions, currentQuestion, isLive, questionOver, gameOver } = this.state;
        let mappedPlayers = this.state.players.map(player => {
            return (
                <p key={player.id + random(200)} className='player-name' ><img style={{borderRadius: 50+'%', width: "60px", height:"auto"}} alt=''  src={'https://oneup.wssu.edu' + player.avatarurl}/>{player.name}</p>
            )
        })
        return (
            <div className='component-container' >
                <div className='triviaid'>
                <p id='player-triviaid'>{courseName}</p>
                <h1>{triviaName}</h1>
                </div> 
                {
                    !isLive && !questionOver && !gameOver ?
                        <div className='btn-players' >
                            <button onClick={() => this.startGame()}className='btn-play' >Play</button>
                            <p className='player-name' id='player-join'>Players joined!</p>
                            {mappedPlayers}
                        </div>
                        :
                        isLive && !questionOver && !gameOver ?
                            <GameQuestions
                                question={questions[currentQuestion].question}
                                answer1={questions[currentQuestion].answer1}
                                answer2={questions[currentQuestion].answer2}
                                answer3={questions[currentQuestion].answer3}
                                answer4={questions[currentQuestion].answer4}
                                questionOver={this.questionOver} />
                            :
                            <GameQuestionOver 
                                nextQuestion={this.nextQuestion} 
                                leaderboard={this.state.leaderBoard} 
                                lastQuestion={this.state.questions.length === this.state.currentQuestion}  />
                }
            </div>
        )
    }
}

export default Game



