import React from 'react';
import Zoom from '../../components/animations/zoomin.js';
import './Game.css';


export default function GameOver(props){
    return(
        <div className='game-over'>
            <Zoom/>
            <h1 className='leaderBoard-title'>Game Over</h1>
            <br/>
            { props.leaderboard.length > 0 &&
            <h2 className='leaderBoard'>1st Place: {props.leaderboard[0].name} <img alt='' style={{borderRadius: 50+'%', width: "60px", height:"auto"}} src={'https://oneup.wssu.edu'+props.leaderboard[0].avatarurl}/></h2>
            }
            { props.leaderboard.length > 1 &&
            <h2 className='leaderBoard'>2nd Place: {props.leaderboard[1].name} <img alt='' style={{borderRadius: 50+'%', width: "60px", height:"auto"}} src={'https://oneup.wssu.edu'+props.leaderboard[1].avatarurl}/></h2>
            }
            { props.leaderboard.length > 2 &&
            <h2 className='leaderBoard'>3rd Place: {props.leaderboard[2].name} <img alt='' style={{borderRadius: 50+'%', width: "60px", height:"auto"}} src={'https://oneup.wssu.edu'+props.leaderboard[2].avatarurl}/></h2>
            }
            <br/>
            <button onClick="window.location.reload();" className='btn-newGame'>
            Start a new Game?
            </button>
        </div> 
    )
}


