import React, { Component } from 'react';
import PlayerQuestions from './Player_Questions';
import PlayerQuestionOver from './Player_Question_Over';
import './Game.css';
import load from '../../Assets/load-circle-outline.svg'
import ReconnectingWebSocket from 'reconnecting-websocket'

function get_cookie(name) {
    var value;
    if (document.cookie && document.cookie !== '') {
        document.cookie.split(';').forEach(function (c) {
            var m = c.trim().match(/(\w+)=(.*)/);
            if(m !== undefined && m[1] == name) {
                value = decodeURIComponent(m[2]);
            }
        });
    }
    return value;
}

class Player extends Component {
    constructor() {
        super()

        this.state = {
            triviaID: get_cookie("triviaid"),
            id: -1,
            triviaidCorrect: false,
            gameStarted: false,
            questionOver: false,
            answerSubmitted: false,
            answeredCorrect: false,
            nickname: '',
            score: 0
        }
        this.submitAnswer = this.submitAnswer.bind(this);
    }
    componentDidMount() {
        console.log("Changing socket: ")
        let ws_scheme = window.location.protocol === "https:" ? "wss" : "ws";
        let connection_url = ws_scheme + '://oneup.wssu.edu/ws/trivia/' + this.state.triviaID;

        this.socket = new ReconnectingWebSocket(connection_url);
        
        this.socket.onopen = function(){
            console.log("Connected to trivia socket");
        }
        this.socket.onclose = function(){
            console.log("Disconnected from trivia socket");
        }

        this.socket.onmessage = function(m){
            var message = JSON.parse(m.data);
            console.log(message)
            if (message.event === 'init_data'){
                this.setState({
                    triviaName: message.trivia_name,
                    courseName: message.course_name,
                    id: message.id,
                    nickname: message.name
                })
                this.socket.send(JSON.stringify({event:'player-joined', name: this.state.nickname}))
                this.socket.send(JSON.stringify({event:'player-add', data: {}}))
            } else if (message.event === 'question-over'){
                this.setState({
                    questionOver: true
                })
            } else if (message.event === 'next-question'){
                this.setState({
                    gameStarted: true,
                    questionOver: false,
                    answerSubmitted: false,
                    answeredCorrect: false
                })
            } else if (message.event === 'sent-info'){
                this.setState({
                    answeredCorrect: message.answeredCorrect,
                    score: this.state.score + message.score
                })
            }
        }.bind(this)
    }
    submitAnswer(num){ 
        this.socket.send(JSON.stringify({event:'question-answered', name: this.state.nickname, answer: num}))
        this.setState({
            answerSubmitted: true
        })
    }
    render() {
        let { gameStarted, questionOver, answerSubmitted } = this.state;
        return (
            <div className='player-container' >
                <div className='status-bar'>
                    <p className='player-info' id='triviaid' >Course: {this.state.courseName}</p>
                </div> 
                {
                    !gameStarted && !questionOver
                    ?
                    <div>
                            <p>You're in!
                             <br />
                                Do you see your nickname on the screen?
                            </p>
                             <div className='answer-container'>
                                    <div className=' q-blank q'></div> 
                                    <div className=' q-blank q'></div> 
                                    <div className=' q-blank q'></div> 
                                    <div className=' q-blank q'></div> 
                             </div> 
                        </div>
                        :
                        gameStarted && !questionOver && !answerSubmitted
                        ?
                        <PlayerQuestions 
                        submitAnswer = {this.submitAnswer} 
                        />
                        :
                        gameStarted && !questionOver && answerSubmitted
                        ?
                        <div className='waiting-for-results' >
                            <p className='answer-indicator' id= 'too-fast'>Did You answer too fast????</p>
                            <img src={load} alt='' className='load-circle' />
                        </div> 
                        :
                        <PlayerQuestionOver
                         answeredCorrect={this.state.answeredCorrect}
                        />
                    }
                    <div className='status-bar' >
                    
                    <p className='player-info'>{this.state.nickname}</p>
                    <div 
                        className={ 
                          gameStarted && !questionOver && answerSubmitted
                          ?
                          'status-bar-hidden'
                          :
                          'status-bar-score'
                        }
                         >{this.state.score}</div> 
                    </div> 
            </div>
        )
    }
}

export default Player;



